import { ListItem,ListItemText,Button } from '@mui/material';
import React from 'react';
import { db } from './firebase';
import './Todo.css';
function Todo({todo,inProgress,id}) {
    const Updateing=()=>{
        db.collection("todos").doc(id).update({
            inProgress:!inProgress
        })
    }
    const deletetodo=()=>{
        db.collection("todos").doc(id).delete()
    }
    return (
        <div style={{display:'flex'}}>
            <ListItem>
                <ListItemText primary={todo} secondary={inProgress?"In ProgressðŸš§":"Completedâœ…"} />
            </ListItem>
            <Button   onClick={Updateing}  >{inProgress?"Done":'UnDone'}</Button>
            <Button   onClick={deletetodo} color="error" >X</Button>
            
        </div>
    )
}

export default Todo
