import { useState,useEffect } from 'react';
import './App.css';
import TextField from '@mui/material/TextField';
import Alert from '@mui/material/Alert';
import Button from '@mui/material/Button';
import { db } from './firebase';
import firebase from 'firebase/compat/app';
import Todo from './Todo';

function App() {
  const [todoInput, setTodoInput] = useState("");
  const [todo, setTodo] = useState([]);
  const[error,setError]=useState(false);
  useEffect(()=>{
    getTodos();
  },[])
  const getTodos=()=>{
      db.collection("todos").onSnapshot(function(querySnapshot){
          setTodo(
              querySnapshot.docs.map((doc)=>(
                {
                  id:doc.id,
                  todo:doc.data().todo,
                  inProgress:doc.data().inProgress,
                }))
          )
      })
  }

  const addTodo=(e)=>{
    e.preventDefault()
    if(todoInput!=""){
      db.collection("todos").add({
        todo:todoInput,
        inProgress:true,
        timestamp:firebase.firestore.FieldValue.serverTimestamp()
      })
      setTodoInput('');
    }else{
      setError(true);
      setTimeout(()=>{
        setError(false);
      },4000)
    }
  }
  return (
    <div className="App" style={{display:'flex',flexDirection:'column', justifyContent:'center',alignItems:"center"}}>
     
        <div >
          <h1>Arbaz Todo's App </h1>
          {error && <Alert style={{marginBottom:'3px'}} severity="error">Please Enter Todo!</Alert>}
          <form>
            <TextField value={todoInput} onChange={(e)=>setTodoInput(e.target.value)} id="standard-basic" label="Write a todo" variant="standard" style={{maxWidth:'300px'}} />
            <Button type="submit" variant="contained"onClick={addTodo} style={{display:'none'}} >Contained</Button>
          </form>
          {
            todo.map((todo)=>(
              <Todo todo={todo.todo} inProgress={todo.inProgress} id={todo.id} />
            ))
          }
        </div>
      
    </div>
  );
}

export default App;
